function swiper(Selector, slides, between) {
  const swiperDesktop = new Swiper(Selector, {
    slidesPerView: slides,
    spaceBetween: between,
    loop: true,

    // If we need pagination
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
      dynamicBullets: true,
    },

    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });
}

export { swiper };
