import { createLazyImage } from '../create-lazy-image';

//money-in-trust
import imageDesktopWorkingUnitsBase from '../../images/index/working-units-image-desktop@0.5.png';
import imageDesktopWorkingUnitsX2 from '../../images/index/working-units-image-desktop@2.png';
import imageDesktopWorkingUnitsX3 from '../../images/index/working-units-image-desktop@3.png';

(() => {
  const imageWrapper = document.querySelector(
    '.working-units-section__image-wrapper'
  );

  const imageMobileBase = imageDesktopWorkingUnitsBase;
  const imageDesktopBase = imageDesktopWorkingUnitsBase;
  const imageMobileX2 = imageDesktopWorkingUnitsX2;
  const imageDesktopX2 = imageDesktopWorkingUnitsX2;
  const imageMobileX3 = imageDesktopWorkingUnitsX3;
  const imageDesktopX3 = imageDesktopWorkingUnitsX3;

  createLazyImage({
    imageDesktopBase,
    imageMobileBase,
    imageDesktopX2,
    imageMobileX2,
    imageDesktopX3,
    imageMobileX3,
    imageWrapper,
  });
})();
