import * as ScrollMagic from 'scrollmagic';
import { TweenMax, TimelineMax, Linear } from 'gsap';

// init controller
let controller = new ScrollMagic.Controller({
  globalSceneOptions: { triggerHook: 'onEnter', duration: 1500 },
});

function scrollMagicParallaxSections(parallaxTrigger, elementSection) {
  // build scenes
  new ScrollMagic.Scene({ triggerElement: parallaxTrigger })
    .setTween(elementSection, {
      y: '80%',
      ease: Linear.easeNone,
    })
    // .addIndicators()
    .addTo(controller);
}

export { scrollMagicParallaxSections };
