import bgImgMobile from '../../images/index/more-uniqueness-mobile.jpg';
import bgImgMobileX2 from '../../images/index/more-uniqueness-mobile@3.jpg';
import bgImgDesktop from '../../images/index/more-uniqueness-desktop.jpg';
import bgImgDesktopX2 from '../../images/index/more-uniqueness-desktop@3.jpg';

import { createLazyImgBg } from '../create-lazy-img-bg';

const containerWrapper = document.querySelector(
  '.more-uniqueness-section__background-img'
);

let bgImg = '';
let bgImgX2 = '';

let screenWidth = window.innerWidth;

const uploadedImage = () => {
  if (screenWidth <= 1169) {
    bgImg = bgImgMobile;
    bgImgX2 = bgImgMobileX2;
    return;
  }
  if (screenWidth >= 1170) {
    bgImg = bgImgDesktop;
    bgImgX2 = bgImgDesktopX2;
    return;
  }
};

uploadedImage();

createLazyImgBg({ containerWrapper, bgImg, bgImgX2 });
