import { ScrollMagicArrowsSvg } from '../ScrollMagicArrowsSvg';

const initScrollMagicArrows = (trigger, pathLine, pathArrow, iconArrow) => {
  ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });

  let screenWidth = window.innerWidth;
  let restart = false;

  if (screenWidth >= 1170) {
    restart = true;
  }

  window.matchMedia('(min-width: 1170px)').addEventListener('change', e => {
    if (!e.matches) return;
    if (restart) return;
    restart = true;

    ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });
  });
};

//trading-bot
(() => {
  const trigger = '#trading-bot__trigger';
  const pathLine = 'path#trading-bot-path-line';
  const pathArrow = 'path#trading-bot-path-arrow';
  const iconArrow = '.trading-bot__icon-arrow';

  initScrollMagicArrows(trigger, pathLine, pathArrow, iconArrow);
})();

