import { createLazyImage } from '../create-lazy-image';

import imageMobileBase from '../../images/index/work-with-us-image-mobile@0.5.png';
import imageDesktopBase from '../../images/index/work-with-us-image-desktop@0.5.png';

import imageMobileX2 from '../../images/index/work-with-us-image-mobile@2.png';
import imageDesktopX2 from '../../images/index/work-with-us-image-desktop@2.png';

import imageMobileX3 from '../../images/index/work-with-us-image-mobile@3.png';
import imageDesktopX3 from '../../images/index/work-with-us-image-desktop@3.png';

const imageWrapper = document.querySelector(
  '.work-with-us-section__image-wrapper'
);

createLazyImage({
  imageDesktopBase,
  imageMobileBase,
  imageDesktopX2,
  imageMobileX2,
  imageDesktopX3,
  imageMobileX3,
  imageWrapper,
});
