import { createLazyImage } from '../create-lazy-image';

//trading-bot
import imageMobileTradingBotBase from '../../images/index/trading-bot-image-mobile@0.5.png';
import imageDesktopTradingBotBase from '../../images/index/trading-bot-image-desktop@0.5.png';
import imageMobileTradingBotX2 from '../../images/index/trading-bot-image-mobile@2.png';
import imageDesktopTradingBotX2 from '../../images/index/trading-bot-image-desktop@2.png';
import imageMobileTradingBotX3 from '../../images/index/trading-bot-image-mobile@3.png';
import imageDesktopTradingBotX3 from '../../images/index/trading-bot-image-desktop@3.png';

(() => {
  const imageWrapper = document.querySelector('.trading-bot__image-wrapper');

  const imageMobileBase = imageMobileTradingBotBase;
  const imageDesktopBase = imageDesktopTradingBotBase;
  const imageMobileX2 = imageMobileTradingBotX2;
  const imageDesktopX2 = imageDesktopTradingBotX2;
  const imageMobileX3 = imageMobileTradingBotX3;
  const imageDesktopX3 = imageDesktopTradingBotX3;

  createLazyImage({
    imageDesktopBase,
    imageMobileBase,
    imageDesktopX2,
    imageMobileX2,
    imageDesktopX3,
    imageMobileX3,
    imageWrapper,
  });
})();

