import axios from 'axios';
const BASE_URL = 'https://api.coingecko.com/api/v3';
axios.defaults.baseURL = BASE_URL;

//       Bitcoin   CoinGecko API
async function fetchCryptoPriceData(cryptoId) {
  const params = new URLSearchParams({
    vs_currency: 'usd',
    days: '30',
    interval: 'daily',
  });

  const response = await axios.get(`coins/${cryptoId}/market_chart?${params}`);

  return response.data;
}

//      
async function getCryptoData(cryptoId) {
  const response = await axios.get(`coins/${cryptoId}`);

  return response.data;
}

export { fetchCryptoPriceData, getCryptoData };
