import Chart from 'chart.js/auto';
import { fetchCryptoPriceData, getCryptoData } from '../api-service-coin-gecko';

//   then()  catch().
async function getDatesAndPrices(crypto, canvasId, label) {
  try {
    const { prices } = await fetchCryptoPriceData(crypto);
    const cryptoData = await getCryptoData(crypto);

    const { uniqueDates, uniquePrices } = getUniqueDatesAndPrices(prices);

    renderCryptoChartItemCanvas(canvasId, cryptoData);

    renderCryptoChart(uniqueDates, uniquePrices, canvasId, label);
  } catch (error) {
    console.log(error);
  }
}

//        
function getUniqueDatesAndPrices(cryptoPrices) {
  //  
  const uniqueDates = cryptoPrices.map(entry =>
    new Date(entry[0]).toLocaleDateString()
  );

  //    
  const uniquePrices = cryptoPrices.map(entry => entry[1]);

  return { uniqueDates, uniquePrices };
}

//    item canvas
function renderCryptoChartItemCanvas(canvasId, cryptoData) {
  //   
  const currentPrice = cryptoData.market_data.current_price.usd;
  const priceChangePercentage24h =
    cryptoData.market_data.price_change_percentage_24h;
  const imageUrlBaseThumb = cryptoData.image.thumb;
  const imageUrlBaseLarge = cryptoData.image.large;
  const coinName = cryptoData.name;
  const coinSymbol = cryptoData.symbol.toUpperCase();

  //   
  const formattedCurrentPrice = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  }).format(currentPrice);

  //  
  const formattedPriceChangePercentage = new Intl.NumberFormat('en-US', {
    style: 'percent',
    minimumFractionDigits: 2,
  }).format(priceChangePercentage24h / 100); //    

  //  
  function changeClaes() {
    if (priceChangePercentage24h > 0) {
      return 'cryptocurrency-chart-list__item-percentage--green';
    } else if (priceChangePercentage24h < 0) {
      return 'cryptocurrency-chart-list__item-percentage--red';
    } else {
      return 'cryptocurrency-chart-list__item-percentage--green';
    }
  }

  //////////

  //desktop
  const markupDesktop = `
    <div class="cryptocurrency-chart-list__item-content">
        <div class="cryptocurrency-chart-list__item-image">
            <img class="lazy" src="${imageUrlBaseThumb}" data-src="${imageUrlBaseLarge}" alt="${coinName}">
        </div>

        <div>
            <h3 class="cryptocurrency-chart-list__item-name">${coinName}</h3>
            <p class="cryptocurrency-chart-list__item-symbol">${coinSymbol}</p>
        </div>
    </div>

    <div class="cryptocurrency-chart-canvas-container">
        <canvas id=${canvasId}-desktop></canvas>
    </div>

    <div>
        <p class="cryptocurrency-chart-list__item-price">${formattedCurrentPrice}</p>
        <p class="cryptocurrency-chart-list__item-percentage ${changeClaes()}">${formattedPriceChangePercentage}</p>
    </div>
  `;

  const cryptocurrencyItemDesktop = document.querySelector(
    `.markets-trade-section__swiper--desktop .cryptocurrency-chart-list__item--${canvasId}`
  );

  cryptocurrencyItemDesktop.innerHTML = markupDesktop;

  //////////

  //mobile
  const markupMobile = `
    <div class="cryptocurrency-chart-list__item-content">
        <div class="cryptocurrency-chart-list__item-image">
            <img class="lazy" src="${imageUrlBaseThumb}" data-src="${imageUrlBaseLarge}" alt="${coinName}">
        </div>

        <div>
            <h3 class="cryptocurrency-chart-list__item-name">${coinName}</h3>
            <p class="cryptocurrency-chart-list__item-symbol">${coinSymbol}</p>
        </div>
    </div>

    <div class="cryptocurrency-chart-canvas-container">
        <canvas id=${canvasId}-mobile></canvas>
    </div>

    <div>
        <p class="cryptocurrency-chart-list__item-price">${formattedCurrentPrice}</p>
        <p class="cryptocurrency-chart-list__item-percentage ${changeClaes()}">${formattedPriceChangePercentage}</p>
    </div>
  `;

  const cryptocurrencyItemMobile = document.querySelector(
    `.markets-trade-section__swiper--mobile .cryptocurrency-chart-list__item--${canvasId}`
  );

  cryptocurrencyItemMobile.innerHTML = markupMobile;

  document.lazyLoadInstance.update();
}

//      
function renderCryptoChart(uniqueDates, uniquePrices, canvasId, label) {
  //   
  const ctxMobile = document
    .getElementById(`${canvasId}-mobile`)
    .getContext('2d');
  const ctxDesktop = document
    .getElementById(`${canvasId}-desktop`)
    .getContext('2d');

  const color = getRandomColor();

  cryptoChart(ctxMobile, uniqueDates, uniquePrices, label, color);
  cryptoChart(ctxDesktop, uniqueDates, uniquePrices, label, color);
}

function cryptoChart(ctx, uniqueDates, uniquePrices, label, color) {
  const cryptoChart = new Chart(ctx, {
    type: 'line',
    data: {
      labels: uniqueDates, //     7  
      datasets: [
        {
          data: uniquePrices, //     7  
          label: `${label} Price (USD)`,
          borderColor: color,
          borderWidth: 2,
          pointRadius: 10,
          pointBorderWidth: 0, //    
          pointHoverBorderWidth: 2, //      
          pointBackgroundColor: 'rgba(0, 0, 0, 0)', //     
        },
      ],
    },
    options: {
      maintainAspectRatio: false,
      scales: {
        x: {
          display: false, //   X
        },
        y: {
          display: false, //   Y
        },
      },
      plugins: {
        legend: {
          display: false, //  
        },
      },
      animation: {
        duration: 2000, //     (, 2000  = 2 )
        easing: 'easeInOutQuad', //   (  'linear', 'easeInOutQuad'  ..)
      },
    },
  });
}

//  
function getRandomColor() {
  const letters = '0123456789ABCDEF';
  let color = '#';
  for (let i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}

