import percentageImageBase from '../../images/index/benefits-with-us-percentage-base.webp';
import percentageImageSvg from '../../images/index/benefits-with-us-percentage-discount.svg';

import skillsImageBase from '../../images/index/benefits-with-us-skills-base.webp';
import skillsImageSvg from '../../images/index/benefits-with-us-skills.svg';

import chartImageBase from '../../images/index/benefits-with-us-chart-base.webp';
import chartImageSvg from '../../images/index/benefits-with-us-chart.svg';

import guaranteedImageBase from '../../images/index/benefits-with-us-guaranteed-base.webp';
import guaranteedImageSvg from '../../images/index/benefits-with-us-guaranteed.svg';

import cautionImageBase from '../../images/index/benefits-with-us-caution-sign-base.webp';
import cautionImageSvg from '../../images/index/benefits-with-us-caution-sign.svg';

const img = [
  { baseImage: percentageImageBase, imageSvg: percentageImageSvg },
  { baseImage: skillsImageBase, imageSvg: skillsImageSvg },
  { baseImage: chartImageBase, imageSvg: chartImageSvg },
  { baseImage: guaranteedImageBase, imageSvg: guaranteedImageSvg },
  { baseImage: cautionImageBase, imageSvg: cautionImageSvg },
];

import { createLazyImageSvg } from '../create-lazy-image-svg';

const imageWrappers = document.querySelectorAll(
  '.benefits-with-us__image-wrapper'
);

imageWrappers.forEach((imageWrapper, index) => {
  const { baseImage, imageSvg } = img[index];

  createLazyImageSvg(baseImage, imageSvg, imageWrapper);
});

//////////

import backgroundImageBase from '../../images/index/benefits-with-us-create-image-background-base.webp';
import backgroundImageSvg from '../../images/index/benefits-with-us-create-image-background.svg';

const backgroundImageWrapper = document.querySelector(
  '.benefits-with-us-section__image-background-wrapper'
);

createLazyImageSvg(
  backgroundImageBase,
  backgroundImageSvg,
  backgroundImageWrapper
);
