//  
import anime from 'animejs';

let limit = 1;
let disabled = false;

//anime.js
let path1 = anime.path('#svg_1');
let path2 = anime.path('#svg_2');
let path3 = anime.path('#svg_3');
let path4 = anime.path('#svg_4');
let path5 = anime.path('#svg_5');
let path6 = anime.path('#svg_6');

let motionMaveStart;
let motionMave1;
let motionMave2;
let motionMave3;
let motionMave4;
let motionMave5;
let motionMave6;
let motionMaveEnd;

function motionMave() {
  motionMaveStart = anime({
    targets: '.start',
    translateX: path1('x'),
    translateY: path1('y'),
    autoplay: false,
  });

  motionMave1 = anime({
    targets: '.target-1',
    translateX: path1('x'),
    translateY: path1('y'),
    autoplay: false,
  });

  motionMave2 = anime({
    targets: '.target-2',
    translateX: path2('x'),
    translateY: path2('y'),
    autoplay: false,
  });

  motionMave3 = anime({
    targets: '.target-3',
    translateX: path3('x'),
    translateY: path3('y'),
    autoplay: false,
  });

  motionMave4 = anime({
    targets: '.target-4',
    translateX: path4('x'),
    translateY: path4('y'),
    autoplay: false,
  });

  motionMave5 = anime({
    targets: '.target-5',
    translateX: path5('x'),
    translateY: path5('y'),
    autoplay: false,
  });

  motionMave6 = anime({
    targets: '.target-6',
    translateX: path6('x'),
    translateY: path6('y'),
    autoplay: false,
  });

  motionMaveEnd = anime({
    targets: '.end',
    translateX: path6('x'),
    translateY: path6('y'),
    direction: 'reverse',
    autoplay: false,
  });
}

motionMave();

//////////

//   
function changeolor(target, color) {
  const elem = document.querySelector(target);

  elem.classList.remove(
    'color-1',
    'color-2',
    'color-3',
    'color-4',
    'color-5',
    'color-6'
  );
  elem.classList.add(color);
}

//////////

//right
const changePathRight = (motionMove, target, path, color) => {
  motionMove = anime({
    targets: target,
    translateX: path('x'),
    translateY: path('y'),
    rotate: path('angle'),
    duration: 500,
    // direction: 'reverse',
    easing: 'linear',
    autoplay: false,
  });
  motionMove.play();
  changeolor(target, color);
};

const byConditionRight = () => {
  if (limit === 1) {
    changePathRight(motionMave1, '.target-1', path1, 'color-1');
    changePathRight(motionMave2, '.target-2', path2, 'color-2');
    changePathRight(motionMave3, '.target-3', path3, 'color-3');
    changePathRight(motionMave4, '.target-4', path4, 'color-4');
    changePathRight(motionMave5, '.target-5', path5, 'color-5');
    changePathRight(motionMave6, '.target-6', path6, 'color-6');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
  if (limit === 2) {
    changePathRight(motionMave1, '.target-1', path2, 'color-2');
    changePathRight(motionMave2, '.target-2', path3, 'color-3');
    changePathRight(motionMave3, '.target-3', path4, 'color-4');
    changePathRight(motionMave4, '.target-4', path5, 'color-5');
    changePathRight(motionMave5, '.target-5', path6, 'color-6');
    changePathRight(motionMave6, '.target-6', path1, 'color-1');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
  if (limit === 3) {
    changePathRight(motionMave1, '.target-1', path3, 'color-3');
    changePathRight(motionMave2, '.target-2', path4, 'color-4');
    changePathRight(motionMave3, '.target-3', path5, 'color-5');
    changePathRight(motionMave4, '.target-4', path6, 'color-6');
    changePathRight(motionMave5, '.target-5', path1, 'color-1');
    changePathRight(motionMave6, '.target-6', path2, 'color-2');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
  if (limit === 4) {
    changePathRight(motionMave1, '.target-1', path4, 'color-4');
    changePathRight(motionMave2, '.target-2', path5, 'color-5');
    changePathRight(motionMave3, '.target-3', path6, 'color-6');
    changePathRight(motionMave4, '.target-4', path1, 'color-1');
    changePathRight(motionMave5, '.target-5', path2, 'color-2');
    changePathRight(motionMave6, '.target-6', path3, 'color-3');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
  if (limit === 5) {
    changePathRight(motionMave1, '.target-1', path5, 'color-5');
    changePathRight(motionMave2, '.target-2', path6, 'color-6');
    changePathRight(motionMave3, '.target-3', path1, 'color-1');
    changePathRight(motionMave4, '.target-4', path2, 'color-2');
    changePathRight(motionMave5, '.target-5', path3, 'color-3');
    changePathRight(motionMave6, '.target-6', path4, 'color-4');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
  if (limit === 6) {
    changePathRight(motionMave1, '.target-1', path6, 'color-6');
    changePathRight(motionMave2, '.target-2', path1, 'color-1');
    changePathRight(motionMave3, '.target-3', path2, 'color-2');
    changePathRight(motionMave4, '.target-4', path3, 'color-3');
    changePathRight(motionMave5, '.target-5', path4, 'color-4');
    changePathRight(motionMave6, '.target-6', path5, 'color-5');
    limit = limit === 6 ? 1 : limit + 1;
    return;
  }
};

//////////

//right
const changePathLeft = (motionMove, target, path, color) => {
  motionMove = anime({
    targets: target,
    translateX: path('x'),
    translateY: path('y'),
    duration: 500,
    direction: 'reverse',
    easing: 'linear',
    autoplay: false,
  });
  motionMove.play();
  changeolor(target, color);
};

const byConditionLeft = () => {
  if (limit === 1) {
    changePathLeft(motionMave1, '.target-1', path6, 'color-5');
    changePathLeft(motionMave2, '.target-2', path1, 'color-1');
    changePathLeft(motionMave3, '.target-3', path2, 'color-1');
    changePathLeft(motionMave4, '.target-4', path3, 'color-2');
    changePathLeft(motionMave5, '.target-5', path4, 'color-3');
    changePathLeft(motionMave6, '.target-6', path5, 'color-4');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
  if (limit === 6) {
    changePathLeft(motionMave1, '.target-1', path5, 'color-4');
    changePathLeft(motionMave2, '.target-2', path6, 'color-5');
    changePathLeft(motionMave3, '.target-3', path1, 'color-1');
    changePathLeft(motionMave4, '.target-4', path2, 'color-1');
    changePathLeft(motionMave5, '.target-5', path3, 'color-2');
    changePathLeft(motionMave6, '.target-6', path4, 'color-3');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
  if (limit === 5) {
    changePathLeft(motionMave1, '.target-1', path4, 'color-3');
    changePathLeft(motionMave2, '.target-2', path5, 'color-4');
    changePathLeft(motionMave3, '.target-3', path6, 'color-5');
    changePathLeft(motionMave4, '.target-4', path1, 'color-1');
    changePathLeft(motionMave5, '.target-5', path2, 'color-1');
    changePathLeft(motionMave6, '.target-6', path3, 'color-2');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
  if (limit === 4) {
    changePathLeft(motionMave1, '.target-1', path3, 'color-2');
    changePathLeft(motionMave2, '.target-2', path4, 'color-3');
    changePathLeft(motionMave3, '.target-3', path5, 'color-4');
    changePathLeft(motionMave4, '.target-4', path6, 'color-5');
    changePathLeft(motionMave5, '.target-5', path1, 'color-1');
    changePathLeft(motionMave6, '.target-6', path2, 'color-1');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
  if (limit === 3) {
    changePathLeft(motionMave1, '.target-1', path2, 'color-1');
    changePathLeft(motionMave2, '.target-2', path3, 'color-2');
    changePathLeft(motionMave3, '.target-3', path4, 'color-3');
    changePathLeft(motionMave4, '.target-4', path5, 'color-4');
    changePathLeft(motionMave5, '.target-5', path6, 'color-5');
    changePathLeft(motionMave6, '.target-6', path1, 'color-1');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
  if (limit === 2) {
    changePathLeft(motionMave1, '.target-1', path1, 'color-1');
    changePathLeft(motionMave2, '.target-2', path2, 'color-1');
    changePathLeft(motionMave3, '.target-3', path3, 'color-2');
    changePathLeft(motionMave4, '.target-4', path4, 'color-3');
    changePathLeft(motionMave5, '.target-5', path5, 'color-4');
    changePathLeft(motionMave6, '.target-6', path6, 'color-5');
    limit = limit === 1 ? 6 : limit - 1;
    return;
  }
};

