//       chart.js
//        

//   ,     
function isElementInViewport(el, offset) {
  let rect = el.getBoundingClientRect();
  let windowHeight =
    window.innerHeight || document.documentElement.clientHeight;

  return (
    rect.top - offset <= windowHeight && //          
    rect.bottom + offset >= 0 //          
  );
}

// ,      
function handleScroll(event) {
  if (disabled) return;

  let delta = event.deltaY || event.wheelDelta;
  let offset = 50; //   100px

  let element = document.getElementById('svg-container');

  if (isElementInViewport(element, offset)) {
    disabled = true;

    if (delta > 0) {
      byConditionRight();
    } else if (delta < 0) {
      byConditionLeft();
    }

    setTimeout(() => {
      disabled = false;
    }, 500);
  }
}

//    
document.addEventListener('wheel', handleScroll);

document.addEventListener('DOMContentLoaded', handleScroll);

byConditionRight();

/////
let screenWidth = window.innerWidth;
let screenChanges = false;

const screenWidthReset = () => {
  if (screenChanges) return;
  screenChanges = true;

  screenWidth = window.innerWidth;

  if (screenWidth <= 1169) {
    disabled = true;
  } else if (screenWidth >= 1170) {
    disabled = false;
    motionMave();
  }

  setTimeout(() => {
    screenChanges = false;
  }, 250);
};

window.addEventListener('resize', screenWidthReset);

screenWidthReset();
