import { scrollMagicRevealOnScroll } from '../ScrollMagicRevealOnScroll';

let screenWidth = window.innerWidth;
let offsetLitecoin = 100;
let offsetBitcoin = 125;
let offsetEthereum = 150;

if (screenWidth <= 1169) {
  offsetLitecoin = 100;
  offsetBitcoin = 150;
  offsetEthereum = 200;
}

if (screenWidth >= 1170) {
  offsetLitecoin = 170;
  offsetBitcoin = 270;
  offsetEthereum = 370;
}

scrollMagicRevealOnScroll(
  '#trading-bot-cryptocurrency-trigger',
  0.9,
  offsetLitecoin,
  '.trading-bot__img-cryptocurrency-litecoin'
);

scrollMagicRevealOnScroll(
  '#trading-bot-cryptocurrency-trigger',
  0.9,
  offsetBitcoin,
  '.trading-bot__img-cryptocurrency-bitcoin'
);

scrollMagicRevealOnScroll(
  '#trading-bot-cryptocurrency-trigger',
  0.9,
  offsetEthereum,
  '.trading-bot__img-cryptocurrency-ethereum'
);
