const interestRateSlider = document.getElementById('interest_rate_slider');
const interestRateInput = document.getElementById('interest_rate');
const sliderValueElement = document.getElementById('slider_rate_value');

noUiSlider.create(interestRateSlider, {
  start: 5000,
  range: {
    min: 0,
    max: 100000,
  },
  step: 100,
  connect: 'lower',
});

let isCustomElementAppended = false;

interestRateSlider.noUiSlider.on('update', function (values, handle) {
  const sliderValue = values[handle];
  interestRateInput.value = sliderValue;
  sliderValueElement.textContent = sliderValue + '$';

  if (!isCustomElementAppended) {
    const handleElement =
      interestRateSlider.querySelector('.noUi-handle-lower');
    handleElement.appendChild(sliderValueElement);

    isCustomElementAppended = true;
  }
});
