import { ScrollMagicArrowsSvg } from '../ScrollMagicArrowsSvg';

//   svg
const trigger = '#work-with-us__trigger';
const pathLine = 'path#work-with-us-path-line';
const pathArrow = 'path#work-with-us-path-arrow';
const iconArrow = '.work-with-us-section__icon-arrow';

ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });

//     .
let screenWidth = window.innerWidth;
let restart = false;

if (screenWidth >= 1170) {
  restart = true;
}

window.matchMedia('(min-width: 1170px)').addEventListener('change', e => {
  if (!e.matches) return;
  if (restart) return;
  restart = true;

  ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });
});
