function createLazyImageSvg(imageBase, imageSvg, imageWrapper) {
  function createImageMarcup() {
    const listCards = `
        <img alt="" class="lazy" src="${imageBase}" data-src="${imageSvg}"/>
    `;

    return listCards;
  }

  imageWrapper.innerHTML = createImageMarcup();
}

export { createLazyImageSvg };
