import * as ScrollMagic from 'scrollmagic';

// init controller
let controller = new ScrollMagic.Controller();

function scrollMagicRevealOnScroll(
  triggerElement,
  triggerHook,
  offset,
  elementReceivingClass
) {
  // build scene
  new ScrollMagic.Scene({
    triggerElement: triggerElement,
    triggerHook: triggerHook,
    offset: offset, // move trigger to center of element
    reverse: false, // only do once
  })
    .setClassToggle(elementReceivingClass, 'visible') // add class toggle
    // .addIndicators() // add indicators (requires plugin)
    .addTo(controller);
}

export { scrollMagicRevealOnScroll };
