import { createLazyImageSvg } from '../create-lazy-image-svg';

import imageBitcoinBase from '../../images/index/trading-bot-bitcoin@0.5.png';
import imageBitcoinSvg from '../../images/index/trading-bot-bitcoin.svg';

import imageLitecoinBase from '../../images/index/trading-bot-litecoin@0.5.png';
import imageLitecoinSvg from '../../images/index/trading-bot-litecoin.svg';

import imageEthereumBase from '../../images/index/trading-bot-ethereum@0.5.png';
import imageEthereumSvg from '../../images/index/trading-bot-ethereum.svg';

const imageWrapperBitcoin = document.querySelector(
  '.trading-bot__img-cryptocurrency-bitcoin'
);
const imageWrapperLitecoin = document.querySelector(
  '.trading-bot__img-cryptocurrency-litecoin'
);
const imageWrapperEthereum = document.querySelector(
  '.trading-bot__img-cryptocurrency-ethereum'
);

createLazyImageSvg(imageBitcoinBase, imageBitcoinSvg, imageWrapperBitcoin);
createLazyImageSvg(imageLitecoinBase, imageLitecoinSvg, imageWrapperLitecoin);
createLazyImageSvg(imageEthereumBase, imageEthereumSvg, imageWrapperEthereum);
