import tradingBotBgImgMobile from '../../images/index/backgraund-body-img-trading-bot-mobile.png';
import tradingBotBgImgDesktop from '../../images/index/backgraund-body-img-trading-bot-desktop.png';

import { createLazyImgBg } from '../create-lazy-img-bg';

//trading-bot__imgBg
(() => {
  const containerWrapper = document.querySelector('.trading-bot__imgBg');

  let bgImg = '';
  let bgImgX2 = '';

  let screenWidth = window.innerWidth;

  const uploadedImage = () => {
    if (screenWidth <= 1169) {
      bgImg = tradingBotBgImgMobile;
      bgImgX2 = tradingBotBgImgMobile;
      return;
    }
    if (screenWidth >= 1170) {
      bgImg = tradingBotBgImgDesktop;
      bgImgX2 = tradingBotBgImgDesktop;
      return;
    }
  };

  uploadedImage();

  createLazyImgBg({ containerWrapper, bgImg, bgImgX2 });
})();

